import { MyLoading, MyCard } from "@macashipo/mlib"
import React from "react"
import { fnListMyPage, KConfigPagePage } from "src/helpers/fnList"

class MUIATTableListCard extends React.Component {
  getData = () => {
    const { cfp } = this.props
    return fnListMyPage.getDataList(cfp.fnList, [])
  }
  getShowPagination = () => {
    const { cfp } = this.props
    const { onePage } = cfp
    if (onePage["UI_IsInDetail"] === true) {
      return false
    }
    return true
  }
  _renderList = () => {
    const { cfp } = this.props
    let _ui = []
    let _data = this.getData()
    console.warn("render List", _data)
    let _configCard =
      fnListMyPage.fnGetConfigPagePage(cfp.fnList, [
        KConfigPagePage.configCard,
      ]) || {}
    if (_data && _data.length > 0) {
      _ui = _data.map((v, i) => {
        return (
          <MyCard
            key={v.Id || i}
            type={_configCard.type}
            config={_configCard}
            data={v}
            fnList={cfp.fnList}
          />
        )
      })
    }
    return _ui
  }
  render() {
    const { cfp } = this.props
    if (cfp) {
      return <div className="mpat-table-list mb-3">{this._renderList()}</div>
    } else {
      return (
        <div
          style={{
            width: "100%",
            textAlign: "center",
          }}
        >
          <MyLoading />
        </div>
      )
    }
  }
}

export default MUIATTableListCard
